import styled from 'styled-components'

export default styled.button`
  height: 40px;
  font-weight: 300;
  background-color: darkgreen;
  border-width: 0px
  border-radius: 5px;
  font-size: 18px;
  text-align: center;
  color: white;
  padding: 0 25px 

  :focus {
    outline: 0;
  }
`



