import styled from 'styled-components'

export default styled.div`
  width: 200px;
  height: 170px;
  line-height: 170px;
  border: 3px solid red;
  border-radius: 5px;
  font-weight: 300;
  font-size: 24px;
  text-align: center;
  vertical-align: middle;
  margin: 5px;
`



